.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
    font-family: 'Trebuchet MS', sans-serif;
    margin: 0; /* Reset margin */
  }
  
  body {
    overflow-x: hidden;
    overflow-y :hidden;  /* Prevent horizontal scrolling */
  }
  
  .container {
    margin-top: -20px;
    display: flex;
    flex-direction: column;
    height: 85vh; /* Keep this for your desired layout */
    background-color: #121C31;
    border: 1px solid white;    
    overflow: hidden; /* Prevents overflow */
    position: relative; 
  }
  
  .Orrery {
    width: 100%; /* Takes 80% of viewport width */
    max-width: 100%; 
    height: 100%; /* Fill the container's height */
    box-sizing: border-box; /* Include padding and border in width/height */
    overflow: hidden; /* Prevent overflow */
    padding: 0; /* Reset padding */
    margin: 0; /* Reset margin */
    position: absolute; /* Prevent overflow and positioning issues */
    top: 0; /* Align with the top */
    left: 0;
  }
  
  .header {
    margin-top: -30px;
  }